<template>
  <div class="login">
    <div class="loginWrapper">
      <img src="../../../static/favicon.png"/>
      <span id="id_of_element_to_blink"> </span>
      <div>
        <!-- <a href="https://soda-phonebook.ga/api/login"> -->
          <button class="google" @click="googleLogin">
            <i class="fa fa-google"></i>
            <span>Google </span>
          </button>
        <!-- </a> -->
      </div>
      <div>
        <button class="facebook" @click="ready">
          <i class="fa fa-facebook-square"></i>
          <span>Facebook </span>
        </button>
      </div>
      <!-- <router-link to="/list"><button @click="loginFunc">  </button></router-link> -->
      <!-- <router-link to="/list"><button class="google">Google </button></router-link> -->
    </div>
    <confirm-modal :show="openConfirmModal" :content="confirmContent" @close="openConfirmModal = false"></confirm-modal>
  </div>
</template>

<script>
import ConfirmModal from '../../utilities/confirmModal/ConfirmModal'
import ConfirmData from '../../utilities/confirmModal/ConfirmData.json'

export default {
  name: 'Login',
  data () {
    return {
      confirmContent: {},
      openConfirmModal: false,
      session: null,
    }
  },
  mounted () {
    // this.blink();
    console.log('login mounted')
    var c = document.cookie;
    console.log('cookie', c)
    var name = 'SESSION';
    var value = document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    this.session = (value ? value[2] : null);
    console.log('session value', value);
    if (document.cookie !== null) {
      console.log('session ')
      // window.location.href = 'https://soda-phonebook.ga/#/list';
    }
  },
  watch: {
    // session () {
    //   if (this.session !== null) {
    //     console.log('session ')
    //     window.location.href = 'https://soda-phonebook.ga/#/list';
    //   }
    // }
  },
  methods: {
    googleLogin () {
      window.location.href = 'https://soda-phonebook.ga/api/login';
    },
    // getCookie () {
    //   var name = 'SESSION';
    //   var value = document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
    //   this.session = (value ? value[2] : null);
    // },
    ready () {
      this.openConfirmModal = true;
      this.confirmContent = ConfirmData['ready'];
    },
    loginTry () {
      console.log(' ');
      this.$http.get(`/me`, {
      }).then((result => {
          console.log('')
          console.log(result)
        }))
        .catch(error => {
          alert(' .')
        })
    },
    loginFunc () {
      console.log('loginFunc')
      this.$emit('login', true);
    }
  },
  components: {
    ConfirmModal,
  }
}
</script>

<style lang="scss">
  @import "Login.scss"
</style>



